// setup an "add a tag" link
const $addTagLink = $('<a href="#" class="btn btn-primary">Ajouter un actionnaire</a>');
const $newLinkLi = $('<div></div>').append($addTagLink);

const $addTagLink2 = $('<a href="#" class="btn btn-primary">Ajouter les pôles clés de votre entreprise</a>');
const $newLinkLi2 = $('<div></div>').append($addTagLink2);

jQuery(document).ready(function() {
    // Get the ul that holds the collection of tags
    const $collectionHolder = $('div.act');
    const $collectionHolder2 = $('div.dep');

    $(".col-form-label").remove();
    // add the "add a tag" anchor and li to the tags ul
    $collectionHolder.append($newLinkLi);
    $collectionHolder2.append($newLinkLi2);

    // count the current form inputs we have (e.g. 2), use that as the new
    // index when inserting a new item (e.g. 2)
    $collectionHolder.data('index', $collectionHolder.find(':input').length);
    $collectionHolder2.data('index', $collectionHolder2.find(':input').length);

    $addTagLink.on('click', function(e) {
        // prevent the link from creating a "#" on the URL
        e.preventDefault();

        // add a new tag form (see code block below)
        addTagForm($collectionHolder, $newLinkLi);
    });
    $addTagLink2.on('click', function(e) {
        // prevent the link from creating a "#" on the URL
        e.preventDefault();

        // add a new tag form (see code block below)
        addTagForm2($collectionHolder2, $newLinkLi2);
    });


});


function addTagForm2($collectionHolder2, $newLinkL2) {
    // Get the data-prototype explained earlier
    const prototype = $collectionHolder2.data('prototype');


    // get the new index
    const index = $collectionHolder2.data('index');

    // Replace '$$name$$' in the prototype's HTML to
    // instead be a number based on how many items we have
    const newForm = prototype.replace(/__name__/g, index);

    // increase the index with one for the next item
    $collectionHolder2.data('index', index + 1);

    // Display the form in the page in an li, before the "Add a tag" link li
    const $newFormLi = $('<div></div>').append(newForm);

    // also add a remove button, just for this example
    $newFormLi.append('<a href="#" class="btn btn-square btn-danger mb-10 remove-dep"><i class="ti-trash"></i></a>');
    $newLinkL2.before($newFormLi);

    // handle the removal, just for this example
    $('.remove-dep').click(function(e) {
        e.preventDefault();

        $(this).parent().remove();

        return false;
    });
}


function addTagForm($collectionHolder, $newLinkLi) {
    // Get the data-prototype explained earlier
    const prototype = $collectionHolder.data('prototype');


    // get the new index
    const index = $collectionHolder.data('index');

    // Replace '$$name$$' in the prototype's HTML to
    // instead be a number based on how many items we have
    const newForm = prototype.replace(/__name__/g, index);

    // increase the index with one for the next item
    $collectionHolder.data('index', index + 1);

    // Display the form in the page in an li, before the "Add a tag" link li
    const $newFormLi = $('<div></div>').append(newForm);

    // also add a remove button, just for this example
    $newFormLi.append('<a href="#" class="btn btn-square btn-danger mb-10 remove-act"><i class="ti-trash"></i></a>');
    $newLinkLi.before($newFormLi);

    // handle the removal, just for this example
    $('.remove-act').click(function(e) {
        e.preventDefault();

        $(this).parent().remove();

        return false;
    });
}


